#hero li::after{transition-duration:.15s;width:0;display:block;background-color:#fff;height:1px;content:''}#hero li:hover::after{width:100%}#hero .menu{float:right}@media (min-width:992px){#hero .menu{margin-right:20px}}#hero li{padding:20px 25px;color:#fff;font-size:22px;cursor:pointer}#hero .hamburger{position:absolute;right:0;z-index:3;padding:20px;border-radius:50%;cursor:pointer}#hero .hamburger__line{display:block;width:40px;height:5px;background-color:#fff;content:'';margin:5px 0;transition-duration:.25s}#hero__menu.opened .menu{display:block!important;margin:0;background-color:rgba(0,0,0,.8);width:100%;position:absolute;left:0;padding-top:80px;padding-bottom:10px;z-index:2}@media (min-width:768px){#hero__menu.opened .menu{display:flex!important;position:unset;background-color:unset;padding:unset;left:unset;width:unset;margin-right:20px}}#hero__menu.opened .hamburger__line:nth-child(1){transform:translateY(10px) rotate(45deg)}#hero__menu.opened .hamburger__line:nth-child(2){opacity:0}#hero__menu.opened .hamburger__line:nth-child(3){transform:translateY(-10px) rotate(-45deg)}